	// $ratingStarWidth - ÑˆÐ¸Ñ€Ð¸Ð½Ð° Ð¸ Ð²Ñ‹ÑÐ¾Ñ‚Ð° Ð¾Ñ‚Ð´ÐµÐ»ÑŒÐ½Ð¾Ð¹ Ð·Ð²ÐµÐ·Ð´Ñ‹ (Ð¿ÐµÑ€ÐµÐ´Ð°Ð²Ð°Ñ‚ÑŒ Ð² Ð¼Ð¸ÐºÑÐ¸Ð½ Ð² px!!)
	// $ratingStarGap - Ñ€Ð°ÑÑÑ‚Ð¾ÑÐ½Ð¸Ðµ Ð¼/Ñƒ Ð·Ð²ÐµÐ·Ð´Ð°Ð¼Ð¸ (Ð¿ÐµÑ€ÐµÐ´Ð°Ð²Ð°Ñ‚ÑŒ Ð² Ð¼Ð¸ÐºÑÐ¸Ð½ Ð² px!!)
	// $icomoon - ÑÐ¸Ð¼Ð²Ð¾Ð» Ð¸Ð· ÑˆÑ€Ð¸Ñ„Ñ‚Ð° icomoon Ð¿Ñ€Ð¸ Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ð½Ð¸Ð¸ Ð² Ñ€Ð¾Ð»Ð¸ Ð·Ð²ÐµÐ·Ð´Ð¾Ñ‡ÐºÐ¸ ÐºÐ°ÑÑ‚Ð¾Ð¼Ð½Ð¾Ð¹ svg-Ð¸ÐºÐ¾Ð½ÐºÐ¸. ÐŸÐµÑ€ÐµÐ´Ð°Ð²Ð°Ñ‚ÑŒ Ð² Ð²Ð¸Ð´Ðµ ÑÑ‚Ñ€Ð¾ÐºÐ¸: '\e900'
	$kef: 1.4;
	display: flex;
	width: ($ratingStarWidth + $ratingStarGap) * 5;
	margin-left: -$ratingStarGap /2;
	margin-right: -$ratingStarGap /2;
	flex-direction: row-reverse;
	font-size: $ratingStarWidth * $kef;
	label{
		width: calc(20% - #{$ratingStarGap});
		// flex: 0 0 calc(20% - #{$ratingStarGap});//ÑÑ‚Ð°Ñ€Ñ‹Ð¹ Ð²Ð°Ñ€Ð¸Ð°Ð½Ñ‚
		margin: 0 $ratingStarGap / 2;
