			line-height: $ratingStarWidth;
			left: 50%;
			transform: translateX(-50%);
			color: $baseColor;
		}
		&:hover{
			&:before{
				color: $activeColor;
			}
			& ~ label:before{
				color: $activeColor;
				// Ð´Ð»Ñ Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ð½Ð¸Ñ Ñ ÐºÐ°ÑÑ‚Ð¾Ð¼Ð½Ð¾Ð¹ Ð¸ÐºÐ¾Ð½ÐºÐ¾Ð¹ Ð²Ð¼ÐµÑÑ‚Ð¾ ÑˆÑ€Ð¸Ñ„Ñ‚Ð¾Ð²Ð¾Ð³Ð¾ ÑÐ¸Ð¼Ð²Ð¾Ð»Ð°
				     // color: transparent;
				     // background-image: url(https://lh5.googleusercontent.com/-DZ5jyR2NRfk/AAAAAAAAAAI/AAAAAAAAACk/I8BNLS9vfCs/photo.jpg?sz=50);
				    // background-size: contain;
				    // background-repeat: no-repeat;
			}
		}

	}
	input{
		position: absolute;
		left: -9999px;

		&:checked ~ label{
			&:before{
