@mixin crystal-rating($baseColor: #000, $activeColor: gold, $fontSize: 40, $spacing: 0,  $icomoon: false) {
	// $spacing Ð¿ÐµÑ€ÐµÐ´Ð°Ð²Ð°Ñ‚ÑŒ Ð±ÐµÐ· 'px'
	// $icomoon - ÑÐ¸Ð¼Ð²Ð¾Ð» Ð¸Ð· ÑˆÑ€Ð¸Ñ„Ñ‚Ð° icomoon Ð¿Ñ€Ð¸ Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ð½Ð¸Ð¸ Ð² Ñ€Ð¾Ð»Ð¸ Ð·Ð²ÐµÐ·Ð´Ð¾Ñ‡ÐºÐ¸ ÐºÐ°ÑÑ‚Ð¾Ð¼Ð½Ð¾Ð¹ svg-Ð¸ÐºÐ¾Ð½ÐºÐ¸. ÐŸÐµÑ€ÐµÐ´Ð°Ð²Ð°Ñ‚ÑŒ Ð² Ð²Ð¸Ð´Ðµ ÑÑ‚Ñ€Ð¾ÐºÐ¸: '\e900'
	  display: flex;
	  align-items: center;
	  display: flex;
	  position: relative;
	  font-size: $fontSize + px;
	  @if $icomoon != false { 
	  	line-height: 1;

  	}@else{
	  	line-height: 0.75;
  	}
	  letter-spacing: $spacing + px;
	  // outline: 1px solid pink;
	  
	  &__body{
	  	position: relative;
	    // width: ($fontSize * 5 * 0.833) + $spacing * 5 * 0.833 + px;
	    // outline: 1px solid;
	    &:before{
	    	display: block;
	    	@if $icomoon != false { 
	    		content: $icomoon $icomoon $icomoon $icomoon $icomoon;
	    		font-family: 'rating' !important;
	    		speak: never;
	    		font-style: normal;
	    		font-weight: normal;
	    		font-variant: normal;
	    		text-transform: none;
	    		-webkit-font-smoothing: antialiased;
	    		-moz-osx-font-smoothing: grayscale;
	    		color: $baseColor;

	    	}@else{
	    		content: 'â˜…â˜…â˜…â˜…â˜…';
	    	}
	    }
	  }
	  &__items{
	    position: absolute;
	    display: flex;
	    width: 100%;
	    height: 100%;
	    top: 0;
	    left: 0;
	    
	    input{
	      flex: 0 0 20%;
	      display: block;
	      height: 100%;
	      opacity: 0;
	      margin: 0;
	    }
	  }
		.crystal-rating__active{
		  position: absolute;
		  width: 0;
		  display: block;
		  height: 100%;
		  top: 0;
		  left: 0;
		  transition: width 0.2s ease-in;
		 
		  &:before{
		  	@if $icomoon != false { 
	    		content: $icomoon $icomoon $icomoon $icomoon $icomoon;
	    		font-family: 'rating' !important;
	    		speak: never;
	    		font-style: normal;
	    		font-weight: normal;
	    		font-variant: normal;
	    		text-transform: none;
	    		-webkit-font-smoothing: antialiased;
	    		-moz-osx-font-smoothing: grayscale;

	    	}@else{
	    		content: 'â˜…â˜…â˜…â˜…â˜…';
	    	}
		    position: absolute;
		    display: block;
		    height: 100%;
		    width: 100%;
		    top: 0;
		    left: 0;
		    color: $activeColor;
		    overflow: hidden;
		  }
		}
		.crystal-rating__value{
		  display: inline-block;
		  font-size: .7em;
		  margin-left: 15px;
		  letter-spacing: normal;
		}
}
